import React, { useState } from "react";
import { Grid } from "@consta/uikit/Grid";
import { TextField } from "@consta/uikit/TextField";
import { Button } from "@consta/uikit/Button";
import { observer } from "mobx-react-lite";

const SignUp = observer(({ User, Localization }) => {
	const [username, setUsername] = useState("");
	const [email, setEmail] = useState("");
	const [password, setPasswod] = useState("");
	return (
		<Grid cols={1} gap="l">
			<Grid cols={1} gap="xs">
				<TextField
					placeholder={Localization.strings.Username}
					value={username}
					onChange={setUsername}
				></TextField>
				<TextField
					placeholder={Localization.strings.Email}
					value={email}
					onChange={setEmail}
				></TextField>
				<TextField
					type="password"
					placeholder={Localization.strings.Password}
					value={password}
					onChange={setPasswod}
				></TextField>
			</Grid>
			<Button
				size="m"
				view="primary"
				label={Localization.strings.SignUp}
				width="default"
				onClick={() => {
					User.signUp(
						username,
						password,
						email,
						Localization.strings
					);
				}}
			/>
		</Grid>
	);
});

export default SignUp;
