import React from "react";
import { Grid, GridItem } from "@consta/uikit/Grid";
import { Button } from "@consta/uikit/Button";
import { observer } from "mobx-react-lite";
import "./MobileApp.css";
import ModalWindowAdd from "../ModalWindowAdd";
import ModalWindowSign from "../ModalWindowSIgn/ModalWindowSign";
import { ThemeToggler } from "@consta/uikit/ThemeToggler";
import TodolistMobile from "../TodoListMobile/TodoListMobile";

const MobileApp = observer(({ Todo, User, Themes, Localization }) => {
	return (
		<Grid className="View" rowGap="m">
			<div></div>
			<ModalWindowSign
				User={User}
				Todo={Todo}
				Localization={Localization}
			></ModalWindowSign>
			<Grid cols={2}>
				<GridItem>
					<ThemeToggler
						items={Themes.items}
						value={Themes.theme}
						getItemKey={(item) => item}
						getItemLabel={(item) => item}
						getItemIcon={Themes.getItemIcon}
						onChange={Themes.saveTheme}
					/>
				</GridItem>
				<GridItem>
					{Localization.language == "ru" ? (
						<Button
							className="LocalButton"
							label={Localization.language}
							onClick={() => {
								Localization.saveLanguage("en");
							}}
						></Button>
					) : (
						<Button
							className="LocalButton"
							label={Localization.language}
							onClick={() => {
								Localization.saveLanguage("ru");
							}}
						></Button>
					)}
				</GridItem>
			</Grid>
			{Todo.todos ? (
				<TodolistMobile
					Todo={Todo}
					Localization={Localization}
				></TodolistMobile>
			) : (
				<></>
			)}

			{User.user.logged ? (
				<ModalWindowAdd
					Todo={Todo}
					Localization={Localization}
				></ModalWindowAdd>
			) : (
				<></>
			)}
		</Grid>
	);
});

export default MobileApp;
