import React, { useState } from "react";
import { Grid } from "@consta/uikit/Grid";
import { TextField } from "@consta/uikit/TextField";
import { Button } from "@consta/uikit/Button";
import { Modal } from "@consta/uikit/Modal";
import { ChoiceGroup } from "@consta/uikit/ChoiceGroup";
import { observer } from "mobx-react-lite";
import { User as Profile } from "@consta/uikit/User";
import LogIn from "./Widgets/LogIn";
import SignUp from "./Widgets/SignUp";

const ModalWindowSign = observer(({ User, Todo, Localization }) => {
	const [items, setItems] = useState([
		Localization.strings.LogIn,
		Localization.strings.SignUp,
	]);
	const [isModalOpen, setIsModalOpen] = useState(false);
	const [choice, setChoice] = useState(items[0]);
	return (
		<div lang={Localization.language}>
			<Grid>
				{User.user.username ? (
					<Grid cols={2} colGap="l">
						<Profile name={User.user.username}></Profile>
						<Button
							size="m"
							view="primary"
							label={Localization.strings.ModalLogOut}
							onClick={() =>
								User.logOut(Localization.strings).then(() =>
									Todo.getTodos()
								)
							}
						/>
					</Grid>
				) : (
					<Button
						size="m"
						view="primary"
						label={Localization.strings.ModalLogIn}
						onClick={() => {
							setIsModalOpen(true);
							setItems([
								Localization.strings.LogIn,
								Localization.strings.SignUp,
							]);
							setChoice(Localization.strings.LogIn);
						}}
					/>
				)}
			</Grid>

			<Modal
				className="Registration"
				isOpen={isModalOpen}
				hasOverlay
				onClickOutside={() => {
					setIsModalOpen(false);
					setChoice(items[0]);
				}}
				onEsc={() => {
					setIsModalOpen(false);
					setChoice(items[0]);
				}}
			>
				<Grid cols={1} gap="l">
					<ChoiceGroup
						value={choice}
						onChange={setChoice}
						items={items}
						getItemLabel={(item) => item}
						multiple={false}
					/>

					{choice === Localization.strings.LogIn ? (
						<LogIn
							User={User}
							Todo={Todo}
							Localization={Localization}
						></LogIn>
					) : (
						<SignUp
							User={User}
							Localization={Localization}
						></SignUp>
					)}
				</Grid>
			</Modal>
		</div>
	);
});

export default ModalWindowSign;
