import React, { useState } from "react";
import { Text } from "@consta/uikit/TextDeprecated";
import { Grid } from "@consta/uikit/Grid";
import { Button } from "@consta/uikit/Button";
import { IconEdit } from "@consta/icons/IconEdit";
import { TextField } from "@consta/uikit/TextField";
import { DatePicker } from "@consta/uikit/DatePicker";
import { Modal } from "@consta/uikit/Modal";
import enLocale from "date-fns/locale/en-US";
import { observer } from "mobx-react-lite";
import ruLocale from "date-fns/locale/ru";

const ModalWindowPut = observer(({ Todo, id, Localization }) => {
	const [isModalOpen, setIsModalOpen] = useState();
	const [date, setDate] = useState(new Date(Todo.todos[id].date));
	const [title, setTitle] = useState(Todo.todos[id].title);
	return (
		<Grid>
			<Button
				size="5xs"
				iconRight={IconEdit}
				onlyIcon={true}
				onClick={() => setIsModalOpen(true)}
			></Button>
			<Modal
				className="Registration"
				isOpen={isModalOpen}
				hasOverlay
				onClickOutside={() => setIsModalOpen(false)}
				onEsc={() => setIsModalOpen(false)}
			>
				<Grid className="Create" cols={1} rowGap="xl">
					<Text>{Localization.strings.ChangeTodo}</Text>
					<TextField
						placeholder={Localization.strings.TodoTitle}
						value={title}
						onChange={setTitle}
					></TextField>
					<DatePicker
						locale={
							Localization.language === "en" ? enLocale : ruLocale
						}
						value={date}
						onChange={setDate}
						placeholder={Localization.strings.TodoDate}
					></DatePicker>
					<Button
						onClick={() => {
							Todo.changeTodo(
								title,
								date,
								Todo.todos[id].id,
								Localization.strings
							).then(() => setIsModalOpen(false));
						}}
						label={Localization.strings.Change}
					></Button>
				</Grid>
			</Modal>
		</Grid>
	);
});

export default ModalWindowPut;
