import React from "react";
import { useState } from "react";
import { Text } from "@consta/uikit/TextDeprecated";
import { Grid } from "@consta/uikit/Grid";
import { TextField } from "@consta/uikit/TextField";
import { DatePicker } from "@consta/uikit/DatePicker";
import { Button } from "@consta/uikit/Button";
import enLocale from "date-fns/locale/en-US";
import { observer } from "mobx-react-lite";
import ruLocale from "date-fns/locale/ru";

const CreateTodo = observer(({ Todo, Localization }) => {
	const [date, setDate] = useState();
	const [title, setTitle] = useState("");

	return (
		<Grid cols={1} rowGap="l">
			<Text>{Localization.strings.CreateTodo}</Text>
			<TextField
				placeholder={Localization.strings.TodoTitle}
				value={title}
				onChange={setTitle}
			></TextField>
			<DatePicker
				locale={Localization.language == "en" ? enLocale : ruLocale}
				value={date}
				onChange={setDate}
				placeholder={Localization.strings.TodoDate}
			></DatePicker>
			<Button
				onClick={() => {
					Todo.addTodo(title, date, Localization.strings);
				}}
				label={Localization.strings.CreateTodo}
			></Button>
		</Grid>
	);
});

export default CreateTodo;
