import React from "react";
import "./App.css";
import { ToastContainer, Bounce } from "react-toastify";
import { Theme } from "@consta/uikit/Theme";
import { useMediaQuery } from "react-responsive";
import Todo from "./Store/Todo";
import User from "./Store/User";
import MobileApp from "./components/MobileApp/MobileApp";
import { observer } from "mobx-react-lite";
import DesktopApp from "./components/DesktopApp/DesktopApp";
import NotifyContainer from "./components/Notifes/NotifyContainer";

const App = observer(({ Themes, Localization }) => {
	const isMobile = useMediaQuery({ query: "(max-width: 500px)" });
	return (
		<Theme className="App" preset={Themes.getTheme(Themes.theme)}>
			{isMobile ? (
				<div>
					<MobileApp
						Localization={Localization}
						Todo={Todo}
						User={User}
						Themes={Themes}
					></MobileApp>
				</div>
			) : (
				<DesktopApp
					Todo={Todo}
					User={User}
					Themes={Themes}
					Localization={Localization}
				></DesktopApp>
			)}
			<NotifyContainer></NotifyContainer>
		</Theme>
	);
});

export default App;
