import React from "react";
import { Grid, GridItem } from "@consta/uikit/Grid";
import { Button } from "@consta/uikit/Button";
import { ThemeToggler } from "@consta/uikit/ThemeToggler";
import { observer } from "mobx-react-lite";

const TogglePanel = observer(({ Themes, Localization }) => {
	return (
		<Grid cols={4}>
			<GridItem col={1}>
				<ThemeToggler
					items={Themes.items}
					value={Themes.theme}
					getItemKey={(item) => item}
					getItemLabel={(item) => item}
					getItemIcon={Themes.getItemIcon}
					onChange={Themes.saveTheme}
				/>
			</GridItem>
			<GridItem>
				{Localization.language == "ru" ? (
					<Button
						className="LocalButton"
						label={Localization.language}
						onClick={() => {
							Localization.saveLanguage("en");
						}}
					></Button>
				) : (
					<Button
						className="LocalButton"
						label={Localization.language}
						onClick={() => {
							Localization.saveLanguage("ru");
						}}
					></Button>
				)}
			</GridItem>
		</Grid>
	);
});

export default TogglePanel;
