import { makeAutoObservable } from "mobx";
import NotifySuccess from "../components/Notifes/NotifySuccess";
import NotifyError from "../components/Notifes/NotifyError";

class User {
	user = { logged: false };

	constructor() {
		makeAutoObservable(this);
		this.isLogged({});
	}

	setLogged(user) {
		this.user = user;
	}

	isLogged = async (user) => {
		if (localStorage.getItem("accessToken")) {
			const request = await fetch(
				`/api/Users/${localStorage.getItem(
					"userId"
				)}?access_token=${localStorage.getItem("accessToken")}`,
				{
					headers: {
						"Content-Type": "application/json;charset=utf-8",
					},
				}
			);
			request.json().then((result) => {
				this.setLogged({
					username: result.username,
					logged: true,
				});
			});
		}
	};

	signUp = async (username, password, email, localization) => {
		if (username && password && email) {
			const request = await fetch("http://localhost:3000/api/Users", {
				method: "POST",
				headers: {
					"Content-Type": "application/json;charset=utf-8",
				},
				body: JSON.stringify({
					username: username,
					email: email,
					password: password,
				}),
			});
			if (request.status === 422) {
				NotifyError(localization.ProvideRealEmail);
			} else {
				NotifySuccess(localization.SignUpSuccess);
			}
		} else {
			NotifyError(localization.ProvideData);
		}
	};

	logIn = async (username, password, localization) => {
		if (username && password) {
			const request = await fetch("/api/Users/login", {
				method: "POST",
				headers: {
					"Content-Type": "application/json;charset=utf-8",
				},
				body: JSON.stringify({
					username: username,
					password: password,
				}),
			});
			if (request.status === 401) {
				NotifyError(localization.LogInFailed);
				return false;
			} else {
				NotifySuccess(localization.LogInSuccess);
				await request.json().then((result) => {
					localStorage.setItem("accessToken", result.id);
					localStorage.setItem("userId", result.userId);
					this.isLogged({});
				});
				return true;
			}
		} else {
			NotifyError(localization.ProvideData);
			return false;
		}
	};

	logOut = async (localization) => {
		await fetch(
			`/api/Users/logout?access_token=${localStorage.getItem(
				"accessToken"
			)}`,
			{
				method: "POST",
				headers: {
					"Content-Type": "application/json;charset=utf-8",
				},
			}
		);
		localStorage.removeItem("accessToken");
		localStorage.removeItem("userId");
		NotifySuccess(localization.LogOut);
		this.setLogged({ logged: false });
	};
}

export default new User();
