import { makeAutoObservable } from "mobx";
import NotifySuccess from "../components/Notifes/NotifySuccess";
import NotifyError from "../components/Notifes/NotifyError";

class Todo {
	todos = null;

	constructor() {
		makeAutoObservable(this);
		this.getTodos();
	}

	setTodos(newTodos) {
		this.todos = newTodos;
	}

	doneTodo = async (id, done) => {
		await fetch(
			`/api/Todos/${id}?access_token=${localStorage.getItem(
				"accessToken"
			)}`,
			{
				method: "PATCH",
				headers: {
					"Content-Type": "application/json;charset=utf-8",
				},
				body: JSON.stringify({
					done: done,
				}),
			}
		);
		this.getTodos();
	};

	changeTodo = async (title, date, id, localization) => {
		if (date && title) {
			await fetch(
				`/api/Todos/${id}?access_token=${localStorage.getItem(
					"accessToken"
				)}`,
				{
					method: "PATCH",
					headers: {
						"Content-Type": "application/json;charset=utf-8",
					},
					body: JSON.stringify({
						title: title,
						date: date,
					}),
				}
			);
			NotifySuccess(localization.ChangeTodo);
			this.getTodos();
		} else {
			NotifyError(localization.ProvideData);
		}
	};

	addTodo = async (title, date, localization) => {
		if (localStorage.getItem("accessToken")) {
			const request = await fetch(
				`/api/Todos?access_token=${localStorage.getItem(
					"accessToken"
				)}`,
				{
					method: "POST",
					headers: {
						"Content-Type": "application/json;charset=utf-8",
					},
					body: JSON.stringify({
						title: title,
						date: date,
						done: false,
					}),
				}
			);
			if (request.status == 422) {
				NotifyError(localization.ProvideData);
			} else {
				NotifySuccess(localization.AddTodo);
			}
		} else {
			NotifyError(localization.FirstLogIn);
		}
		this.getTodos();
	};

	deleteTodo = async (key, localization) => {
		await fetch(
			`/api/Todos/${
				this.todos[key].id
			}?access_token=${localStorage.getItem("accessToken")}`,
			{
				method: "DELETE",
				headers: {
					"Content-Type": "application/json;charset=utf-8",
				},
			}
		);
		NotifySuccess(localization.DeleteTodo);
		this.getTodos();
	};
	getTodos() {
		if (!localStorage.getItem("accessToken")) {
			this.setTodos(null);
		} else {
			fetch(
				`/api/Todos?access_token=${localStorage.getItem(
					"accessToken"
				)}`,
				{
					headers: {
						"Content-Type": "application/json;charset=utf-8",
					},
				}
			)
				.then((result) => {
					return result.json();
				})
				.then((data) => {
					if (data.length == 0) {
						this.setTodos(null);
						return;
					}
					this.setTodos(data);
				});
		}
	}
}

export default new Todo();
