import { toast, Bounce } from "react-toastify";

const NotifyError = (title) =>
	toast.error(title, {
		position: "top-center",
		autoClose: 3000,
		hideProgressBar: false,
		closeOnClick: false,
		pauseOnHover: true,
		draggable: true,
		progress: undefined,
		theme: "colored",
		transition: Bounce,
	});

export default NotifyError;
