import React, { useState } from "react";
import { Grid } from "@consta/uikit/Grid";
import { TextField } from "@consta/uikit/TextField";
import { Button } from "@consta/uikit/Button";
import { observer } from "mobx-react-lite";

const LogIn = observer(({ User, Todo, Localization }) => {
	const [username, setUsername] = useState("");
	const [password, setPasswod] = useState("");
	return (
		<Grid cols={1} gap="l">
			<Grid cols={1} gap="xs">
				<TextField
					placeholder={Localization.strings.Username}
					value={username}
					onChange={setUsername}
				></TextField>

				<TextField
					type="password"
					placeholder={Localization.strings.Password}
					value={password}
					onChange={setPasswod}
				></TextField>
			</Grid>
			<Button
				size="m"
				view="primary"
				label={Localization.strings.LogIn}
				width="default"
				onClick={async () => {
					if (localStorage.getItem("accessToken")) {
					} else {
						if (
							await User.logIn(
								username,
								password,
								Localization.strings
							)
						) {
							Todo.getTodos();
						}
					}
				}}
			></Button>
		</Grid>
	);
});

export default LogIn;
