import React, { useState } from "react";
import { Grid } from "@consta/uikit/Grid";
import { TextField } from "@consta/uikit/TextField";
import { Button } from "@consta/uikit/Button";
import { Modal } from "@consta/uikit/Modal";
import { observer } from "mobx-react-lite";
import { IconAdd } from "@consta/icons/IconAdd";
import { Text } from "@consta/uikit/TextDeprecated";
import { DatePicker } from "@consta/uikit/DatePicker";
import enLocale from "date-fns/locale/en-US";
import ruLocale from "date-fns/locale/ru";

const ModalWindowAdd = observer(({ Todo, Localization }) => {
	const [isModalOpen, setIsModalOpen] = useState();
	const [date, setDate] = useState();
	const [title, setTitle] = useState("");
	return (
		<div className="ModalView">
			<Button
				size="l"
				iconSize="l"
				className="ButtonAdd"
				iconRight={IconAdd}
				onlyIcon={true}
				onClick={() => setIsModalOpen(true)}
			></Button>

			<Modal
				className="Registration"
				isOpen={isModalOpen}
				hasOverlay
				onClickOutside={() => setIsModalOpen(false)}
				onEsc={() => setIsModalOpen(false)}
			>
				<Grid cols={1} gap="l">
					<Text>{Localization.strings.CreateTodo}</Text>
					<TextField
						placeholder={Localization.strings.TodoTitle}
						value={title}
						onChange={setTitle}
					></TextField>
					<DatePicker
						locale={
							Localization.language == "en" ? enLocale : ruLocale
						}
						value={date}
						onChange={setDate}
						placeholder={Localization.strings.TodoDate}
					></DatePicker>
					<Button
						onClick={() =>
							Todo.addTodo(title, date).then(() =>
								setIsModalOpen(false)
							)
						}
						label={Localization.strings.Create}
					></Button>
				</Grid>
			</Modal>
		</div>
	);
});

export default ModalWindowAdd;
