import LocalizedStrings from "react-localization";
import { makeAutoObservable } from "mobx";
import { IconMoon } from "@consta/icons/IconMoon";
class Localization {
	items = ["ru", "en"];
	language = "ru";

	constructor() {
		makeAutoObservable(this);
		this.setLanguage();
	}

	strings = new LocalizedStrings({
		en: {
			TodoTitle: "Enter title",
			TodoDate: "DD/MM/YYYY",
			ModalLogOut: "Log out",
			ModalLogIn: "Log in/Sign up",
			Email: "Enter email",
			Password: "Enter password",
			Username: "Enter username",
			Create: "Create",
			Change: "Change",
			ChangeTodo: "Change todo",
			CreateTodo: "Create todo",
			LogIn: "Log in",
			SignUp: "Sign up",
			TodoList: "You have no todos",
			ProvideData: "Provide data",
			ProvideRealEmail: "Provide real email",
			SignUpSuccess: "Sign up success. Now log in",
			LogInFailed: "Log in failed",
			LogInSuccess: "Log in success",
			LogOut: "Successfully logged out",
			ChangeTodo: "You successfully changed a Todo",
			AddTodo: "You have created a Todo",
			FirstLogIn: "First log in",
			DeleteTodo: "You have deleted a Todo",
		},
		ru: {
			TodoTitle: " ",
			TodoDate: "//",
			ModalLogOut: "",
			ModalLogIn: "/",
			Email: " ",
			Password: " ",
			Username: " ",
			Create: "",
			Change: "",
			ChangeTodo: " ",
			CreateTodo: " ",
			LogIn: "",
			SignUp: "",
			TodoList: "   ",
			ProvideData: " ",
			ProvideRealEmail: "  ",
			SignUpSuccess: " .   ",
			LogInFailed: "  ",
			LogInSuccess: "  ",
			LogOut: "  ",
			ChangeTodo: "  ",
			AddTodo: "  ",
			FirstLogIn: " ",
			DeleteTodo: "  ",
		},
	});

	setLanguage = () => {
		if (localStorage.getItem("Localization") == "ru") {
			this.strings.setLanguage("ru");
			this.language = "ru";
		} else {
			this.strings.setLanguage("en");
			this.language = "en";
		}
	};

	saveLanguage = (item) => {
		localStorage.setItem("Localization", item);
		this.strings.setLanguage(item);
		this.language = item;
	};
}

export default new Localization();
