import React from "react";
import { Text } from "@consta/uikit/TextDeprecated";
import { Grid, GridItem } from "@consta/uikit/Grid";
import { Button } from "@consta/uikit/Button";
import { observer } from "mobx-react-lite";
import { Card } from "@consta/uikit/Card";
import ModalWindowPut from "../../ModalWindowPut";
import { IconTrash } from "@consta/icons/IconTrash";
import { Theme, presetGpnDark } from "@consta/uikit/Theme";
import { Checkbox } from "@consta/uikit/Checkbox";

const MobileCardActive = observer(({ element, Localization, Todo, id }) => {
	const getDate = (el) => {
		const date = new Date(el.date);
		const showenDate = `${date.getDate()}.${
			date.getMonth() + 1
		}.${date.getFullYear()}`;
		return showenDate;
	};
	return (
		<Card
			className="MobileCardActive"
			verticalSpace="m"
			horizontalSpace="m"
			key={id}
		>
			<Grid cols={4} gap="xs">
				<GridItem col={3}>
					<Grid gap="m">
						<Theme preset={presetGpnDark}>
							<Text align="left">{element.title}</Text>
						</Theme>
						<Theme preset={presetGpnDark}>
							<Text align="left">{getDate(element)}</Text>
						</Theme>
					</Grid>
				</GridItem>
				<GridItem col={1}>
					<Grid gap="xs" className="GridButtons">
						<GridItem>
							<Checkbox
								checked={element.done}
								onChange={() => {
									Todo.doneTodo(
										Todo.todos[id].id,
										element.done ? false : true
									);
								}}
							></Checkbox>
						</GridItem>
						<GridItem>
							<ModalWindowPut
								Todo={Todo}
								id={id}
								Localization={Localization}
							></ModalWindowPut>
						</GridItem>
						<GridItem>
							<Button
								onClick={() => Todo.deleteTodo(id)}
								size="5xs"
								iconRight={IconTrash}
								onlyIcon={true}
							></Button>
						</GridItem>
					</Grid>
				</GridItem>
			</Grid>
		</Card>
	);
});

export default MobileCardActive;
