import React from "react";
import { ToastContainer, Bounce } from "react-toastify";

const NotifyContainer = () => (
	<ToastContainer
		position="top-center"
		autoClose={5000}
		hideProgressBar={false}
		newestOnTop={false}
		closeOnClick={false}
		rtl={false}
		pauseOnFocusLoss
		draggable
		pauseOnHover
		theme="colored"
		transition={Bounce}
	></ToastContainer>
);

export default NotifyContainer;
