import React from "react";
import { Text } from "@consta/uikit/TextDeprecated";
import { Grid, GridItem } from "@consta/uikit/Grid";
import { Card } from "@consta/uikit/Card";
import { Button } from "@consta/uikit/Button";
import { IconTrash } from "@consta/icons/IconTrash";
import ModalWindowPut from "../../ModalWindowPut";
import { Theme, presetGpnDark } from "@consta/uikit/Theme";
import { Checkbox } from "@consta/uikit/Checkbox";
import { observer } from "mobx-react-lite";
const CardActive = observer(({ Todo, Localization, element, id }) => {
	const getDate = (date) => {
		const showenDate = `${date.getDate()}.${
			date.getMonth() + 1
		}.${date.getFullYear()}`;
		return showenDate;
	};
	return (
		<Card
			className="CardInactive"
			verticalSpace="m"
			horizontalSpace="m"
			key={id}
		>
			<Grid cols={4} gap="m">
				<GridItem col={3}>
					<Grid gap="m">
						<Theme preset={presetGpnDark}>
							<Text>{element.title}</Text>
						</Theme>
						<Theme preset={presetGpnDark}>
							<Text>{getDate(new Date(element.date))}</Text>
						</Theme>
					</Grid>
				</GridItem>
				<GridItem col={1}>
					<Grid gap="m" className="GridButtons">
						<GridItem>
							<Checkbox
								checked={element.done}
								onChange={() => {
									Todo.doneTodo(
										Todo.todos[id].id,
										element.done ? false : true,
										Localization.strings
									);
								}}
							></Checkbox>
						</GridItem>
						<GridItem>
							<ModalWindowPut
								Todo={Todo}
								id={id}
								Localization={Localization}
							></ModalWindowPut>
						</GridItem>
						<Button
							iconRight={IconTrash}
							onlyIcon={true}
							onClick={() =>
								Todo.deleteTodo(id, Localization.strings)
							}
							size="5xs"
						></Button>
					</Grid>
				</GridItem>
			</Grid>
		</Card>
	);
});

export default CardActive;
