import React from "react";
import { Grid } from "@consta/uikit/Grid";
import ModalWindowSign from "../../ModalWindowSIgn/ModalWindowSign";
import { observer } from "mobx-react-lite";
import CreateTodo from "./CreateTodo";
import TogglePanel from "./TogglePanel";

const LeftPanel = observer(({ Todo, User, Themes, Localization }) => {
	return (
		<Grid className="Create" cols={1} rowGap="xl">
			<ModalWindowSign
				User={User}
				Todo={Todo}
				Localization={Localization}
			></ModalWindowSign>

			<TogglePanel
				Themes={Themes}
				Localization={Localization}
			></TogglePanel>

			<CreateTodo Todo={Todo} Localization={Localization}></CreateTodo>
		</Grid>
	);
});

export default LeftPanel;
