import { makeAutoObservable } from "mobx";
import NotifySuccess from "../components/Notifes/NotifySuccess";
import NotifyError from "../components/Notifes/NotifyError";
import { IconSun } from "@consta/icons/IconSun";
import { IconMoon } from "@consta/icons/IconMoon";
import { Theme, presetGpnDark, presetGpnDefault } from "@consta/uikit/Theme";

class Themes {
	items = ["Default", "Dark"];
	theme = "Dark";

	constructor() {
		makeAutoObservable(this);
		this.setTheme();
	}

	setTheme = () => {
		if (localStorage.getItem("Theme") == "Dark") {
			this.theme = "Dark";
		} else {
			this.theme = "Default";
		}
	};

	getItemIcon = (item) => {
		if (item === "Default") {
			return IconSun;
		}
		if (item === "Dark") {
			return IconMoon;
		}
	};

	saveTheme = (item) => {
		localStorage.setItem("Theme", item);

		this.theme = item;
	};

	getTheme = (item) => {
		if (item == "Default") {
			return presetGpnDefault;
		}
		if (item == "Dark") {
			return presetGpnDark;
		}
	};
}

export default new Themes();
