import React from "react";
import { Text } from "@consta/uikit/TextDeprecated";
import { Grid } from "@consta/uikit/Grid";
import { observer } from "mobx-react-lite";
import "./TodoList.css";
import CardActive from "./Widgets/CardActive";
import CardInactive from "./Widgets/CardInactive";

const Todolist = observer(({ Todo, Localization }) => {
	return (
		<div>
			<Grid
				cols={1}
				gap="xl"
				breakpoints={{
					1280: { cols: 3 },
					480: { cols: 2 },
				}}
			>
				{Todo.todos != null ? (
					Todo.todos.map((element, key) => {
						console.log("cock");
						return element.done ? (
							<CardInactive
								Todo={Todo}
								Localization={Localization}
								element={element}
								id={key}
								key={key}
							></CardInactive>
						) : (
							<CardActive
								Todo={Todo}
								Localization={Localization}
								element={element}
								id={key}
								key={key}
							></CardActive>
						);
					})
				) : (
					<Text>{Localization.strings.TodoList}</Text>
				)}
			</Grid>
		</div>
	);
});

export default Todolist;
