import React from "react";
import "./DesktopApp.css";
import { Layout } from "@consta/uikit/Layout";
import Todolist from "../TodoList/Todolist";
import { observer } from "mobx-react-lite";
import LeftPanel from "./Widgets/LeftPanel";

const DesktopApp = observer(({ Todo, User, Themes, Localization }) => {
	return (
		<div lang={Localization.language}>
			<Layout>
				<Layout className="Layout1" flex={1}>
					<LeftPanel
						User={User}
						Themes={Themes}
						Todo={Todo}
						Localization={Localization}
					></LeftPanel>
				</Layout>
				<Layout className="Layout2" flex={2}>
					<Todolist
						Todo={Todo}
						Localization={Localization}
					></Todolist>
				</Layout>
			</Layout>
		</div>
	);
});

export default DesktopApp;
