import React from "react";
import { observer } from "mobx-react-lite";
import { ListBox, ListItem } from "@consta/uikit/ListCanary";
import MobileCardActive from "./Widgets/MobileCardActive";
import MobileCardInactive from "./Widgets/MobileCardInactive";
import { useVirtualScroll } from "@consta/uikit/useVirtualScroll";
import "./TodoListMobile.css";

const TodolistMobile = observer(({ Todo, Localization }) => {
	const { listRefs, scrollElementRef, slice, spaceTop } = useVirtualScroll({
		length: Todo.todos ? Todo.todos.length : 0,
		isActive: true,
	});
	return (
		<ListBox ref={scrollElementRef} className="ListTodos">
			<div>
				{Todo.todos.slice(...slice).map((element, id) => {
					return (
						<ListItem
							key={`${id}${spaceTop}`}
							ref={listRefs[slice[0] + id]}
							label={
								element.done ? (
									<MobileCardInactive
										Todo={Todo}
										id={id}
										element={element}
										Localization={Localization}
									></MobileCardInactive>
								) : (
									<MobileCardActive
										Todo={Todo}
										id={id}
										element={element}
										Localization={Localization}
									></MobileCardActive>
								)
							}
						/>
					);
				})}
			</div>
		</ListBox>
	);
});

export default TodolistMobile;
